
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapState, mapActions} from 'vuex';

export default {
data () {
return {
name: '首页',
menuList: ['homepage', 'cardInfo', 'cardRecharge', 'diagnose', 'cardUsedInfo'],
activeMenu: 'homepage',
entryUrl: document.location.href,
showMenu: false,
menus: {
'language.noop': '<span class="menu-title">Language</span>',
'zh-CN': '中文',
'en': 'English'
},
drawerVisibility: false,
showModeValue: 'push',
showPlacementValue: 'right'
};
},
computed: {
...mapState({
route: state => state.route,
routeName: state => state.route.name,
path: state => state.route.path,
currentPid: state => state.currentPid,
deviceready: state => state.app.deviceready,
demoTop: state => state.vux.demoScrollTop,
isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction
}),
activeMenuIndex () {
return this.menuList.indexOf(this.routeName);
},
isShowNav () {
if (this.entryUrl.indexOf('hide-nav') > -1) {
return false;
}
return true;
},
leftOptions () {
return {
showBack: this.route.path !== '/'
};
},
rightOptions () {
return {

};
},
headerTransition () {
if (!this.direction) return '';
return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left';
},
componentName () {
if (this.route.path) {
const parts = this.route.path.split('/');
if (/component/.test(this.route.path) && parts[2]) return parts[2];
}
},
isDemo () {
return /component|demo/.test(this.route.path);
},
title () {
return this.route.meta.title;
},
viewTransition () {
if (!this.direction) return '';
return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out');
}
},
directives: {
TransferDom
},
components: {
Radio,
Group,
Cell,
Badge,
Drawer,
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet
},
methods: {
fetchData () {
let currentPid = this.route.params.pid;
if (!currentPid && currentPid !== 0) {
this.$router.replace({path: '/error'});
}
this.$store.commit('setCurrentPid', currentPid);



},
initData () {
},
changePage (pathName) {
this.drawerVisibility = false;
let path = '/' + this.currentPid + '/' + pathName;
this.$router.push({path});
},
onShowModeChange (val) {
/** hide drawer before changing showMode **/
this.drawerVisibility = false;
setTimeout(one => {
this.showModeValue = val;
}, 400);
},
onPlacementChange (val) {
/** hide drawer before changing position **/
this.drawerVisibility = false;
setTimeout(one => {
this.showPlacementValue = val;
}, 400);
},
onClickMore () {
this.showMenu = true;
},
changeLocale (locale) {
this.$i18n.set(locale);
this.$locale.set(locale);
},
...mapActions([
'updateDemoPosition'
])
},
mounted () {
this.fetchData();
this.initData();
},
watch: {},
created () {
},
beforeDestory () {
},
destoryed () {
},
beforeRouteEnter (to, from, next) {
next();
},
beforeRouteUpdate (to, from, next) {
next();
},
beforeRouteLeave (to, from, next) {
next();
}

};



